import React, { useCallback } from "react";
import { SubmitHandler, useForm } from "react-hook-form";
import { Icon } from "../../components";
import Input from "../../components/Input";
import { userLogin } from "../../utils/api";
import {
  ACCESS_TOKEN,
  CURRENT_TIME,
  EXPIRE_TIME,
  REFRESH_TOKEN,
  URL,
} from "../../utils/constants";
import { getCookie, setCookie } from "../../utils/cookies";
import * as S from "./LoginPage.style";

interface Props {
  isLogin: (bool: boolean) => void;
}
interface IFormInput {
  email: string;
  password: string;
}

const LoginPage = ({ isLogin }: Props) => {
  const {
    register,
    handleSubmit,
    formState: { isSubmitting, errors },
  } = useForm<IFormInput>();

  const onSubmit: SubmitHandler<IFormInput> = useCallback(
    async (data, e) => {
      if (e) e.preventDefault();
      const { email, password } = data;

      try {
        const res = await userLogin({ email, password });
        if (!res) throw new Error("   ");
        const { accessToken, refreshToken } = res;

        await setCookie(ACCESS_TOKEN, URL, accessToken, true);
        await setCookie(REFRESH_TOKEN, URL, refreshToken, true);
        await setCookie(
          EXPIRE_TIME,
          URL,
          (CURRENT_TIME + 18000).toString(),
          false
        );

        getCookie(ACCESS_TOKEN, URL);

        isLogin(true);
      } catch (error) {
        alert("    .");
        console.log(error);
      }
    },
    [isLogin]
  );

  return (
    <S.Container onSubmit={handleSubmit(onSubmit)}>
      <S.IconWrapper>
        <S.LogoIconWrapper href="https://linkbook.tk/" target="_blank">
          <Icon name="logo" width={35} height={20} />
        </S.LogoIconWrapper>
        <S.CloseIconWrapper>
          <Icon
            name="btn_x"
            width={13}
            height={13}
            onClick={() => window.close()}
          />
        </S.CloseIconWrapper>
      </S.IconWrapper>
      <S.Title>
        <S.MainText>Linkbook</S.MainText>  ! 
      </S.Title>
      <S.InputContainer>
        <div>
          <Input
            placeholder="()"
            type="text"
            {...register("email", {
              required: "  .",
              pattern: {
                value: /\S+@\S+\.\S+/,
                message: "   .",
              },
            })}
          />
          {errors.email && (
            <S.ErrorText role="alert">{errors.email.message}</S.ErrorText>
          )}
        </div>
        <div>
          <Input
            placeholder=""
            type="password"
            {...register("password", {
              required: "  .",
            })}
          />
          {errors.password && (
            <S.ErrorText role="alert">{errors.password.message}</S.ErrorText>
          )}
        </div>
      </S.InputContainer>
      <S.ButtonContainer>
        <S.LoginButton disabled={isSubmitting}></S.LoginButton>
      </S.ButtonContainer>
    </S.Container>
  );
};

export default LoginPage;
